.Dashboard {
  background-color: #F5F7FB;
  padding: 32px 24px; }
  .Dashboard .title {
    font-family: Usual;
    font-weight: 700;
    font-size: 2rem;
    align-items: flex-start;
    color: #272E4C; }
  .Dashboard .sub-title {
    font-family: Usual;
    font-weight: 600;
    font-size: 1.25rem;
    color: #9EA3AC;
    margin-bottom: 16px; }
  .Dashboard .content {
    min-height: 200px;
    border-radius: 12px;
    margin-bottom: 16px;
    overflow: hidden;
    box-shadow: 0px 3px 6px #272E4C19; }
  .Dashboard .loading-indicator {
    color: green [500];
    justify-content: 'center';
    margin: auto;
    z-index: -1; }
  .Dashboard .card-header {
    height: 124px;
    background-color: #FAFCFE;
    padding: 16px; }
  .Dashboard .card-graph-header {
    padding: 16px 0px;
    margin: 0px 16px; }
  .Dashboard .card-content {
    min-height: 200px;
    padding: 0px 16px; }
  .Dashboard .card-footer {
    padding: 16px; }
  .Dashboard .dashboard-temp-graph-img-report {
    height: 240px;
    max-width: 100%;
    margin: 16px 0px; }
  .Dashboard .dashboard-temp-graph-img {
    width: 100%;
    margin: 16px 0px; }
  .Dashboard .dashboard-graph-title {
    font-family: Usual;
    font-weight: 600;
    font-size: 1.5rem;
    color: #151C1B; }
  .Dashboard .dashboard-count {
    font-size: 2.25rem; }
  .Dashboard .dashboard-count-description {
    font-family: Usual;
    font-weight: 600;
    font-size: 0.875rem;
    color: #151C1B; }
  .Dashboard .dashboard-table-hg-even-row {
    background-color: #FFF1F1;
    padding: 4px 0px; }
  .Dashboard .dashboard-table-even-row {
    background-color: #FFFFFF;
    padding: 4px 0px; }
  .Dashboard .dashboard-table-hg-odd-row {
    background-color: #F7EAEC;
    padding: 4px 0px; }
  .Dashboard .dashboard-table-odd-row {
    background-color: #FAFCFE;
    padding: 4px 0px; }
  .Dashboard .dashboard-table-header-cell {
    font-family: Usual;
    font-weight: 700;
    font-size: 0.875rem;
    color: #3C394A;
    min-width: 150px; }
  .Dashboard .dashboard-table-data-cell {
    font-family: Usual;
    font-size: 0.875rem;
    color: #151C1B;
    cursor: default; }
  .Dashboard .dashboard-table-data-cell-sm {
    font-family: Usual;
    font-size: 0.75rem;
    color: #151C1B;
    cursor: default; }
  .Dashboard .dashboard-table-data-cell-sm:hover {
    cursor: pointer; }
  .Dashboard .dashboard-table-data-cell-link {
    font-family: Usual;
    font-size: 0.75rem;
    color: #151C1B;
    text-decoration: underline;
    cursor: pointer; }
  .Dashboard .status-circle {
    font-size: 8px;
    color: #9EA3AC; }
  .Dashboard .status-circle-open {
    color: #41b67f; }
  .Dashboard .status-circle-pending {
    color: #f8b344; }
  .Dashboard .status-circle-resolved {
    color: #7f3aab; }
  .Dashboard .status-circle-closed {
    color: #29b1c3; }
  .Dashboard .urgency-indicator {
    font-family: Usual;
    font-size: 0.6rem;
    font-weight: 600;
    width: 16px;
    height: 16px;
    border-radius: 4px; }
  .Dashboard .urgency-1 {
    color: white;
    background: #fa5656; }
  .Dashboard .urgency-2 {
    color: white;
    background-color: #f8b344; }
  .Dashboard .urgency-3 {
    color: white;
    background-color: #41b67f; }
  .Dashboard .dashboard-list-item-text {
    font-family: Usual;
    font-size: 0.875rem;
    color: #151C1B; }
  .Dashboard .dashboard-list-item-text2 {
    font-family: Usual;
    font-size: 0.875rem;
    color: rgba(21, 28, 27, 0.54); }
  .Dashboard .dashboard-list-item-link {
    font-family: Usual;
    font-weight: 700;
    font-size: 0.875rem;
    color: #0D5E40;
    text-decoration: none; }
  .Dashboard .dashboard-list-item-link2 {
    font-family: Usual;
    font-weight: 700;
    font-size: 0.875rem;
    color: rgba(13, 94, 64, 0.54);
    text-decoration: none; }
  .Dashboard .dashboard-view-more-link {
    font-family: Usual;
    font-weight: 700;
    font-size: 0.875rem;
    color: #0D5E40;
    text-decoration: none; }
  .Dashboard .dashboard-list-item-link-disabled {
    font-family: Usual;
    font-weight: 700;
    font-size: 0.875rem;
    color: #0d5e4080;
    text-decoration: none; }
  .Dashboard .dashboard-table-data-cell-disabled {
    font-family: Usual;
    font-size: 0.875rem;
    color: #151c1b80; }
  .Dashboard .dashboard-claim-link {
    color: #3AB77D;
    font-weight: bold;
    font-size: 14px;
    line-height: 17px; }

.login-main {
  left: 0;
  top: 0;
  height: 100%;
  position: fixed;
  background: #E7E9EF 0% 0% no-repeat padding-box;
  font-family: 'Usual', sans-serif;
  opacity: 1;
  font-size: 14px;
  color: #151C1B; }
  .login-main .login-shape-1 {
    left: 0;
    top: 0;
    width: 45%;
    height: 110%;
    position: fixed; }
  .login-main .login-shape-2 {
    top: 0;
    right: 0;
    position: fixed; }
  .login-main .login-block {
    padding: 0 10%; }
    .login-main .login-block .login-logo {
      padding-top: 28px;
      width: 74px; }
    .login-main .login-block .login-project-name {
      padding-top: 4rem;
      padding-bottom: 4rem;
      font-weight: 600;
      width: 386px;
      height: 44px;
      text-align: left;
      font-size: 32px;
      letter-spacing: 0;
      color: #272E4C;
      opacity: 1; }
    .login-main .login-block .login-button {
      margin-top: 56px;
      width: 100%;
      height: 48px;
      background: #3AB77D 0% 0% no-repeat padding-box;
      border-radius: 8px;
      opacity: 1;
      color: #FFFFFF; }

.login-input-area {
  padding-top: 28px; }
  .login-input-area .login-text {
    font-size: 14px; }
  .login-input-area .login-text-field {
    width: 100%;
    padding: 10px 16px;
    font-size: 16px;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border: 0.5px solid #272e4c4d;
    border-radius: 8px;
    font-family: 'Usual';
    margin-top: 10px; }
    .login-input-area .login-text-field.MuiInputBase-input {
      font-family: 'Usual', serif; }
  .login-input-area.MuiInputBase-input {
    font-family: 'Usual', serif; }

